/**
 * Склоенине слов, первым аргументом передаётся число
 * а вторым массив из трёх слов, которые выбираются в зависимости
 * от количества
 * @param value number
 * @param words string[]
 * @returns string
 */
function prettyWord(value, words) {
    value = Math.abs(value) % 100;
    const num = value % 10;
    if (value > 10 && value < 20)
        return words[2];
    if (num > 1 && num < 5)
        return words[1];
    if (num == 1)
        return words[0];
    return words[2];
}
export { prettyWord };
